"use server"

import { redirect } from "next/navigation"
import { workers, workerPasswords } from "./mockData"

export interface LoginResult {
  success: boolean
  error?: string
  redirect?: string
}

export async function loginWorker(
  companyId: string,
  workerId: string,
  password: string
): Promise<LoginResult> {
  // Simulate network delay
  await new Promise((resolve) => setTimeout(resolve, 800))

  // Find the worker
  const worker = workers.find((w) => w.id === workerId)

  // Verify worker exists and belongs to the company
  if (!worker) {
    return {
      success: false,
      error: "Worker not found",
    }
  }

  if (worker.companyId !== companyId) {
    return {
      success: false,
      error: "Worker does not belong to this company",
    }
  }

  // Verify password
  const correctPassword = workerPasswords[workerId]
  if (!correctPassword || correctPassword !== password) {
    return {
      success: false,
      error: "Incorrect password",
    }
  }

  // In a real app, you would:
  // 1. Create a session token
  // 2. Set cookies or store session in database
  // 3. Return user data

  // Redirect to dashboard (this will throw, which is expected in Next.js)
  redirect("/")
}

export async function logout(): Promise<void> {
  // In a real app, you would:
  // 1. Clear session token
  // 2. Remove cookies
  // 3. Invalidate session in database
  
  // For now, just redirect to login page
  redirect("/login")
}

